<script>
import Utils  from '../utils/utils'
import Web3 from 'web3'
import loadweb3 from "../mixins/getWeb3";
import HfPopUp from "../components/elements/HfPopUp.vue"
import Loading from 'vue-loading-overlay'
import "vue-loading-overlay/dist/vue-loading.css";
import { mapActions, mapGetters, mapState, mapMutations } from 'vuex'

import notificationMixins from "../mixins/notificationMixins";
import {  fetchTokenPrice, getNativeBalance, getTokenBalance } from './moralis';
export default {
    components: { HfPopUp, Loading },
    data() {
        return {
            isLoading: false,
            fullPage: true,
            showSaveButton: false,
            chainIdChainMap: {
                "1": {
                    moralisChainName: 'eth',
                    chainName: "Ethereum",
                    token: "ETH",
                    logo: "eth_logo.svg",
                    logoPath: 'https://token-icons.s3.amazonaws.com/eth.png',
                    hex: "0x1",
                    wrappedTokenAddress: "0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2"
                },
                "137": {
                    moralisChainName: 'polygon',
                    chainName: "Polygon",
                    token: "MATIC",
                    logo: "matic-token.png",
                    hex: "0x89",
                    wrappedTokenAddress: "0x0d500B1d8E8eF31E21C99d1Db9A6444d3ADf1270"

                },
                "56": {
                    moralisChainName: 'bsc',
                    chainName: "BSC",
                    token: "BNB",
                    logo: "bnb.png",
                    hex: "0x38",
                    wrappedTokenAddress: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c"

                }
            },
            web3: null,
            walletAddress: {
                address: "",
                chainId: ""
            },
            walletAddressEdit: {
                address: "",
                chainId: ""
            },
            proof: [],
            success: false,
            didId: "",
            isEditing: false,
            walletsPortfolio: [],
        }
    },

    watch: {
        didDocument: {
            handler(newValue, oldValue) {
                if (Object.keys(oldValue).length <= 0) {
                    return;
                }
                this.showSaveButton = true;
            },
            deep: true
        }
    },

    computed: {
        ...mapGetters(["getEntityHeader", "getDIDDocJSONString"]),
        ...mapState(["didDocument", "did"]),
        totalBalance() {
            if (this.walletsPortfolio.length > 0) {
                let val = 0

                this.walletsPortfolio.forEach(token => {
                    val += token.value
                })
                return val
            } else {
                return 0
            }

        },        
    },

    async created() {
        await this.authenticateSSIAppAction()
    },
    methods: {
        ...mapActions(['authenticateSSIAppAction','resolveADID','updateADID', 'createADID', 'registerADID']),
        ...mapMutations(['setDIDDocument', 'setDID', 'addVerificationMethod','cleanStates']),
        async loadAllBalances() {
            const verMethods = this.didDocument.verificationMethod;
            if (verMethods && verMethods.length > 0) {
                verMethods.forEach(async eachVerMeth => {
                    const parsedBCAId = this.parseBlockchainAccountId(eachVerMeth.blockchainAccountId)
                    const walletAddress = parsedBCAId.walletAddress

                    const network = parsedBCAId.chainData.moralisChainName
                    const wrappedTokenAddress = parsedBCAId.chainData.wrappedTokenAddress;
                    const symbol = parsedBCAId.chainData.token

                    const t = this.walletsPortfolio.find(x => (x.walletAddress == walletAddress && x.network === network && x.symbol === symbol))
                    if (!t) {
                        this.loadBalance({
                            walletAddress,
                            chain: network,
                            wrappedTokenAddress,
                            logo: parsedBCAId.chainData.logo,
                            logoPath: parsedBCAId.chainData.logoPath,
                            symbol
                        })
                    } else {
                        console.log('Already loaded.... ');
                    }
                })
            } else {
                console.log('this.didDoc.verificationMethod is undefined')
            }
        },

        async loadBalance({ walletAddress, chain, wrappedTokenAddress, logo = '', logoPath = '', symbol = '' }) {
            const nativeTokenData = await getNativeBalance({ address: walletAddress, chain })

            if (nativeTokenData && nativeTokenData.balance > 0) {

                const nativeTokenPrice = await fetchTokenPrice(wrappedTokenAddress, chain)

                const nativeBalance = nativeTokenData.balance / 1e18;

                this.walletsPortfolio.push({
                    walletAddress,
                    balance: nativeBalance.toFixed(4),
                    name: chain,
                    symbol,
                    network: chain,
                    logoPath,
                    logo,
                    price: (nativeTokenPrice.usdPrice.toFixed(4)),
                    value: (parseFloat(nativeTokenPrice.usdPrice) * parseFloat(nativeBalance))
                })
            } else {
                console.log('No balance found for ' + chain)
            }



            const USDCERC20ContractAddress = '0xa0b86991c6218b36c1d19d4a2e9eb0ce3606eb48'
            let response = await getTokenBalance({
                walletAddress,
                chain,
                tokenAddresses: [USDCERC20ContractAddress] // token address of USDC
            });
            if (response && response.length > 0) {
                response = response[0]
                if (response.balance > 0) {
                    const usdcTokenPrice = await fetchTokenPrice(USDCERC20ContractAddress, chain)
                    const usdcbal = response.balance / 1e6;
                    this.walletsPortfolio.push({
                        balance: usdcbal.toFixed(4),
                        logoPath: response.logo,
                        symbol: response.symbol,
                        name: response.name,
                        network: chain,
                        price: (usdcTokenPrice.usdPrice.toFixed(4)),
                        value: (parseFloat(usdcTokenPrice.usdPrice) * parseFloat(usdcbal))
                    })
                } else {
                    console.log('No balance found for USDC')
                }
            } else {
                console.log('No balance found for USDC')
            }


            const HIDERC20WalletAddress = {
                'ETH': '0xB14eBF566511B9e6002bB286016AB2497B9b9c9D', // contract address of hid on eth
                'MATIC': '0x87847703D4bb4FCD42DB887FfdcB94496e77e3ab' // contract address of hid on polygon
            }

            if (HIDERC20WalletAddress[symbol]) {
                let hidResponse = await getTokenBalance({
                    walletAddress,
                    chain,
                    tokenAddresses: [HIDERC20WalletAddress[symbol]] // token address of USDC
                });
                if (hidResponse && hidResponse.length > 0) {
                    hidResponse = hidResponse[0]
                    if (hidResponse.balance > 0) {
                        const usdcTokenPrice = await fetchTokenPrice(HIDERC20WalletAddress[symbol], chain)
                        const usdcbal = hidResponse.balance / 1e18;
                        this.walletsPortfolio.push({
                            balance: usdcbal.toFixed(4),
                            logo: 'HS_White.png',
                            logoPath: 'https://etherscan.io/token/images/hypersign_32.png',
                            symbol: hidResponse.name,
                            name: hidResponse.name,
                            network: chain,
                            price: (usdcTokenPrice.usdPrice.toFixed(4)),
                            value: (parseFloat(usdcTokenPrice.usdPrice) * parseFloat(usdcbal))
                        })
                    } else {
                        console.log('No balance found for HID ')
                    }
                } else {
                    console.log('No balance found for HID')
                }
            }

        },

        openWalletPopup(id = 'conn-wallet') {
            this.$root.$emit('bv::show::modal', id)
        },

        closeWalletPopup(id = 'conn-wallet') {
            this.$root.$emit('bv::hide::modal', id)
        },

        parseBlockchainAccountId(bId) {
            if (bId) {
                const bIds = bId.split(':');
                return {
                    walletAddress: bIds[2],
                    chainId: bIds[1],
                    chainData: this.chainIdChainMap[bIds[1]]
                }
            }
        },

        async ok(chainId) {
            this.isLoading = true
            this.web3 = await loadweb3(chainId)
            const accounts = await this.web3.eth.getAccounts();
            if (!this.isEditing) {                
                this.walletAddress = {
                    address: "",
                    chainId: ""
                }
                this.walletAddress.address = accounts[0]
                this.walletAddress.chainId = `${chainId}`
            } else {
                this.walletAddressEdit = {
                    address: "",
                    chainId: ""
                }
                this.walletAddressEdit.address = accounts[0]
                this.walletAddressEdit.chainId = `${chainId}`
            }
            this.closeWalletPopup()
            this.isLoading = false
            await this.sign()

            //// TODO: open this later
            //this.loadAllBalances()
        },

        addWallet() {
            this.isEditing = true;
            this.openWalletPopup();
        },

        async verify(message, walletAddress, signature) {
            let recoveredWalletAddress = await this.web3.eth.personal.ecRecover(message, signature);
            recoveredWalletAddress = Web3.utils.toChecksumAddress(recoveredWalletAddress)
            if (recoveredWalletAddress !== walletAddress) {
                return false
            }
            return true
        },

        async sign() {
            try {
                this.isLoading = true
                if (!this.isEditing) {
                    if (!this.walletAddress) {
                        throw new Error('Wallet address is not set')
                    }

                    let didId = `did:hid:testnet:${this.walletAddress.address}`
                    
                    // Step 0:  Resolve a DID and see if its already
                    const data = await this.resolveADID({didId})
                    if (data) {
                        this.setDIDDocument(data)
                        this.setDID(didId)
                        this.isLoading = false;
                        // //// TODO: open this later
                        this.loadAllBalances()
                        return;
                    } else {
                        console.error(data)
                    }
                    // Step 1:  Create a DID 
                    const payload = {
                            walletAddress: this.walletAddress.address,
                            chainId: this.web3.utils.toHex(this.walletAddress.chainId)
                    }
                    await this.createADID(payload)

                    // Step 1:  Sign the DID using eth personal sign
                    const sig = await this.web3.eth.personal.sign( this.getDIDDocJSONString, this.walletAddress.address)
                    
                    // Step 3:  Register the DID on hypersign network
                    const vmId = this.didDocument.verificationMethod.find(x => x.id.includes(this.walletAddress.address) ? x.id : undefined)
                    await this.registerADID({
                        verificationMethodId: vmId.id,
                        signature: sig
                    })


                    this.loadAllBalances()
                } else {
                    if (!this.walletAddressEdit) {
                        throw new Error('Wallet address is not set')
                    }
                    
                    if (!this.didDocument) {
                        return this.notifyErr('DID doc is not generated')
                    }
                    
                    const blockchainId = `eip155:${this.walletAddressEdit.chainId}:${this.walletAddressEdit.address}`;
                    await this.addVerificationMethod({ blockchainId })
                    
                    // const signerVm = this.didDocument.verificationMethod.find(x => x.blockchainAccountId == bcAccountId);
                    // if (!signerVm) {
                    //     console.error("No verification method found")
                    //     return;
                    // }
                    // const sig = await this.web3.eth.personal.sign(
                    //     this.getDIDDocJSONString, this.walletAddressEdit.address
                    // )           
                }
                this.isLoading = false
            } catch (e) {
                this.clear()
                this.notifyErr(e.message)
            } finally{
                this.isLoading = false
                this.didId = this.did
            }
        },

        async saveChanges() {
            try {
                this.isLoading = true

                if (!this.did) {
                    throw new Error('No DID to update')
                }
                const didDocResolved = await this.resolveADID({ didId: this.did })

                if (didDocResolved) {
                    // if exists
                    const allWallets = didDocResolved.verificationMethod.map(x => x.blockchainAccountId);

                    const t = allWallets.find(x => x.indexOf(this.walletAddress.address) > 0)
                    const chainID = this.web3.utils.toHex(t.split(':')[1])
                    await ethereum.request({
                        method: 'wallet_switchEthereumChain',
                        params: [{ chainId: chainID, }]
                    })

                    if (!t) {
                        this.isEditing = false;
                        this.isLoading = false

                        throw new Error(`Connect either of these accounts to save ` + allWallets.toString())
                    }


                    // Get the signature 
                    const message = this.getDIDDocJSONString
                    const sig = await this.web3.eth.personal.sign(
                        message, this.walletAddress.address
                    )
                    // verify the signateur

                    // Update the dID
                    this.updateDID(sig, this.walletAddress.address, message);

                    return;
                } else {
                    // else then generate a new
                    console.log('Could not resolve the DID for id = ' + this.did)

                }

                this.isLoading = false
            } catch (e) {
                this.isLoading = false

                this.notifyErr(e.message)

            }

        },

        async updateDID(sig = '', walletAddress = '', message = '') {

            try {

                if (!this.did) {
                    throw new Error('No DID to update')
                }

                if (sig && walletAddress) {
                    const verified = await this.verify(message, walletAddress, sig)

                    if (!verified) {
                        throw new Error('Invaild signature')
                    }
                }

                //TODO: update (start from hhere)
                const payload = {                    
                    verificationMethodId: JSON.parse(message).verificationMethod.find(x => x.blockchainAccountId.includes(this.walletAddressEdit.address)).id,
                    signature: sig,
                }
                this.isLoading = true
                this.isEditing = false
                const data = await this.updateADID(payload)
                
                if (data.statusCode === 400) {
                    throw new Error(data.message)
                }
                this.isLoading = false
                this.notifySuccess('Successfully updated')
                this.clear()
            } catch (e) {
                this.isLoading = false

                this.notifyErr(e.message)
            }
        },

        async resolve() {
            try {
                this.isLoading = true
                this.cleanStates()
                this.clear();
                if (!this.didId.startsWith('did:hid:')) {
                    throw new Error('Enter a valid did')
                }
                const data = await this.resolveADID({didId: this.didId})
                if(data){
                    this.setDIDDocument(data)
                    this.setDID(this.didId)
                    this.loadAllBalances()
                }else {
                    throw new Error('Could not resolve the DID ' + this.did)
                }
            } catch (e) {
                this.notifyErr(e.message);
            } finally {
                this.isLoading = false
            }
        },

        clear() {
            this.isEditing = false
            this.showSaveButton = false
        },

    },

    mixins: [notificationMixins, Utils]

}
</script>
